.container {
  background-color: #ffffff;

  border: none;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.5);

  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border-radius: 5px;
}

.sub-container {
  max-width: 500px;
  margin: 20px auto;
  overflow: auto;
  min-height: 300px;
  padding: 20px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

  background-color: #3998AD;
  color: #ffffff;
  padding: 20px;
}

.footer {
  margin: 10px;
}

.btn {
  display: inline-block;
  background: #59C9A5;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn:hover {
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
}

.btn-block {
  display: block;
  width: 100%;

  margin-bottom: 20px;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;

  border-radius: 2px;
}

.task.reminder {
  border-left: 5px solid #59C9A5;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.close-icon {
  position: relative;
  top: 50%;
}

.list-container {
  overflow-y: scroll;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}
